import React, { Component } from 'react'
import { Layout, Menu, } from 'antd';
import { Link } from "react-router-dom";
import "./index.less";

const { SubMenu } = Menu;
const { Sider } = Layout;

export default class index extends Component {
    state = {
        href: "/"
    }
    componentDidMount() {
        let oDiv = document.getElementById("sider-content"),
            H = 0,
            Y = oDiv;
        while (Y) {
            H += Y.offsetTop;
            Y = Y.offsetParent;
        }
        window.onscroll = function () {
            var s = document.body.scrollTop || document.documentElement.scrollTop;
            if (s > H) {
                oDiv.style = "position:fixed;top:0;";
            } else {
                oDiv.style = "";
            }
        }
        // let href = window.location.href;
        // let index1 = href.lastIndexOf("/");
        // let str1 = href.substring(index1 + 1, href.length);
        // href = href.substr(0, href.lastIndexOf("/", index1))
        // let index2 = href.lastIndexOf("/");
        // let str2 = href.substring(index2 + 1, href.length);
        // this.setState({ href: str2+str1 });
    }
    render() {

        return (
            <Sider width={200} className="site-layout-background" id="sider">
                <div id="sider-content">
                    <div className="personal">
                        <img src={require("../../assets/images/avatar.jpg")} alt="头像" />
                    </div>
                    <Menu
                        mode="inline"
                        style={{ height: 'calc(100% - 200px)', borderRight: 0 }}
                        defaultOpenKeys={['gojs']}
                        defaultSelectedKeys={[this.state.href]}
                    >
                        <SubMenu
                            key="gojs"
                            title={<span>Go JS</span>}

                        >
                            <Menu.Item key="gojsintro"><Link to="/gojs/intro">介绍</Link></Menu.Item>
                            <Menu.Item key="gojs1"><Link to="/gojs/1">第一节</Link></Menu.Item>
                            <Menu.Item key="gojs2"><Link to="/gojs/2">第二节</Link></Menu.Item>
                            <Menu.Item key="4">option4</Menu.Item>
                        </SubMenu>
                    </Menu>
                </div>
            </Sider>
        )
    }
}
